import * as React from 'react';
import { Helmet } from 'react-helmet';
import { graphql, navigate, useStaticQuery } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import { styled } from '@mui/system';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Tab from '@mui/material/Tab';
import Tabs from '@mui/material/Tabs';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import LocalOfferIcon from '@mui/icons-material/LocalOffer';
import StyleIcon from '@mui/icons-material/Style';
import Link from './link';

const black = '#2e353f';
const blue = '#005b99';

const theme = createTheme({
  palette: {
    primary: {
      main: blue
    },
    text: {
      primary: black
    },
    action: {
      /*
      active: '#00ff00',
      selected: '#00ff00',
      focus: '#00ff00',
      hover: '#00ff00'
      */
    }
  },
  typography: {
    h1: {
      fontSize: 24,
      fontWeight: 'bold'
    },
    h2: {
      fontSize: 20,
      fontWeight: 'bold'
    },
    h3: {
      fontSize: 16,
      fontWeight: 'bold'
    },
    body: {
      fontSize: 14
    },
    body2: {
      fontSize: 12
    }
  }
});

const StyledComponent = styled('div')({
  fontFamily:
    "'Helvetica Neue', 'Arial', 'Hiragino Kaku Gothic ProN', 'Hiragino Sans', 'Meiryo', 'sans-serif'",
  color: black,
  lineHeight: 1.8,
  blockquote: {
    margin: 0,
    paddingLeft: 10,
    borderLeft: `2px solid ${blue}`,
    color: '#666',
    fontStyle: 'italic'
  }
});

const Layout = ({
  children,
  location,
  title: pageTitle,
  description: pageDescription,
  thumbnail
}) => {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
          siteUrl
        }
      }
      allMarkdownRemark(limit: 2000) {
        group(field: frontmatter___tags) {
          fieldValue
          totalCount
        }
      }
    }
  `);

  const { title: siteTitle, siteUrl } = data.site.siteMetadata;
  const title = pageTitle ? `${pageTitle} | ${siteTitle}` : siteTitle;
  const description = pageDescription ? pageDescription : title;

  const isToppage = location.pathname === '/';
  const url = siteUrl + decodeURIComponent(location.pathname);
  const thumbnailUrl = thumbnail || `${siteUrl}/ogp.png`;

  const { group } = data.allMarkdownRemark;

  return (
    <ThemeProvider theme={theme}>
      <Helmet
        htmlAttributes={{
          lang: 'ja',
          prefix: 'og: https://ogp.me/ns#'
        }}
      >
        <title>{title}</title>
        <meta name='description' content={description} />
        <meta property='og:title' content={title} />
        <meta property='og:site_name' content={siteTitle} />
        <meta property='og:description' content={description} />
        <meta property='og:type' content={isToppage ? 'website' : 'article'} />
        <meta property='og:locale' content='ja_JP' />
        <meta property='og:url' content={url} />
        <meta property='og:image' content={thumbnailUrl} />
        <meta property='og:image:url' content={thumbnailUrl} />
        <meta property='og:image:secure_url' content={thumbnailUrl} />
      </Helmet>
      <StyledComponent>
        <AppBar
          position='fixed'
          sx={{
            backgroundColor: '#2e353f'
          }}
        >
          <Container>
            <Toolbar>
              <Link href='/'>
                <Box>
                  <StaticImage
                    src='../images/sheep-white-512x512.png'
                    alt='SHEEP NULL'
                    layout='fixed'
                    width={40}
                    height={40}
                    placeholder='blurred'
                  />
                </Box>
              </Link>
              <Tabs
                ml={4}
                value={false}
                variant='scrollable'
                scrollButtons
                allowScrollButtonsMobile
              >
                <Tab
                  icon={
                    <StyleIcon
                      sx={{
                        fontSize: 14
                      }}
                    />
                  }
                  iconPosition='start'
                  label=''
                  disableFocusRipple={true}
                  disableRipple={true}
                  sx={{
                    opacity: location.pathname === '/tags/' ? 0.5 : 1.0,
                    pointerEvents: location.pathname === '/tags/' ? 'none' : '',
                    mx: 2,
                    color: '#fff'
                  }}
                  onClick={() => navigate('/tags/')}
                />
                {group
                  .filter(({ totalCount }) => totalCount > 1)
                  .sort((first, second) => second.totalCount - first.totalCount)
                  .map(({ fieldValue, totalCount }, i) => {
                    const uri = `/tags/${fieldValue}/`;
                    const isCurrent =
                      decodeURIComponent(location.pathname) === uri;

                    return (
                      <Tab
                        key={i}
                        icon={
                          <LocalOfferIcon
                            sx={{
                              fontSize: 14
                            }}
                          />
                        }
                        iconPosition='start'
                        label={
                          <>
                            {fieldValue}
                            <small>({totalCount})</small>
                          </>
                        }
                        disableFocusRipple={true}
                        disableRipple={true}
                        sx={{
                          opacity: isCurrent ? 0.5 : 1.0,
                          pointerEvents: isCurrent ? 'none' : '',
                          mx: 2,
                          color: '#fff',
                          textTransform: 'none'
                        }}
                        onClick={() => navigate(`/tags/${fieldValue}/`)}
                      />
                    );
                  })}
              </Tabs>
            </Toolbar>
          </Container>
        </AppBar>
        <Container
          maxWidth='md'
          sx={{
            mt: 14
          }}
        >
          <Box component='header'></Box>
          <Box component='main'>{children}</Box>
          <Box component='footer'>
            <Typography align='center' variant='body2'>
              &copy; {new Date().getFullYear()} SHEEP NULL
            </Typography>
          </Box>
        </Container>
      </StyledComponent>
    </ThemeProvider>
  );
};

export default Layout;
