import * as React from 'react';
import { navigate } from 'gatsby';
import MuiLink from '@mui/material/Link';

const Link = props => {
  const onClick = e => {
    e.preventDefault();
    navigate(props.href);
  };

  return (
    <MuiLink onClick={onClick} href={props.href} {...props}>
      {props.children}
    </MuiLink>
  );
};

export default Link;
